.banner {
  background-color: #f2c680;
  &__container {
    padding: 15px 0;
    text-align: center;
  }
  &__text {
    font-size: 24px;
    font-family: RalewayBold;
    color: #fff;
    line-height: 1.2em;
    text-shadow: 0px 0px 9px #1e4b6833;
  }
  .decorate {
    padding: 0 0.5em;
  }
}

@media all and(min-width: 576px) {
  .banner__text {
    font-size: 28px;
    .decorate_style6:after {
      width: 100%;
      left: 0;
    }
  }
  .banner__container {
    padding: 25px 0;
  }
}
@media all and(min-width:992px) {
  .banner__text {
    font-size: 32px;
    br {
      display: none;
    }
  }
  .banner__container {
    padding: 40px 0;
  }
}
@media all and(min-width:1200px) {
  .banner__text {
    font-size: 48px;
  }
  .banner__container {
    padding: 70px 0;
  }
}